import React from 'react';
import { BrowserRouter as Router, Switch, Link, Route } from 'react-router-dom';
import Home from './components/Home';
import Users from './components/Users';

import './App.css';

export default function App() {
	return (
		<div className="App">
			<Router>
				<nav>
					<li>
						<Link to="/react-simple">Home</Link>
					</li>
					<li>
						<Link to="/react-simple/users">Users</Link>
					</li>
				</nav>
				<Switch>
					<Route exact path="react-simple">
						<Home />
					</Route>
					<Route path="react-simple/users">
						<Users />
					</Route>
				</Switch>
			</Router>
		</div>
	);
}
